import '../../styles/model/ModelDetails.css';
import { useEffect, useState } from 'react'
import { useParams } from "react-router-dom";
import StatementList from '../statement/StatementList';

const ModelDetails = () => {
    const { id, name } = useParams();
    const [statements, setStatements] = useState(null);


    useEffect(() => {
        fetch(`https://warm-harbor-43713.herokuapp.com/http://ec2-34-241-179-74.eu-west-1.compute.amazonaws.com/api/model/${id}/statements`)
            .then(response => response.json())
            .then(data => {
                setStatements(data);
            })
            .catch(err => console.log(err));
    }, []);
    return (

        <div>
            <h2> {name} </h2>
            {statements !== null ? <StatementList statements={statements} /> : undefined}

        </div>


    );
}

export default ModelDetails;
