import '../../styles/model/Model.css';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrash } from '@fortawesome/free-solid-svg-icons'

const Model = ({ id, name, statements }) => {

    const handleOnClick = (e) => {
        e.preventDefault();

        const requestOptions = {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json', "Access-Control-Allow-Origin": "*" }
        };
        fetch(`https://warm-harbor-43713.herokuapp.com/http://ec2-34-241-179-74.eu-west-1.compute.amazonaws.com/api/model/${id}`, requestOptions)
            .then(response => {
                if (response.status === 200) return response.json();

            })
            .then(data => {
                alert("Model Usunieto!");
                window.location.reload(false);
            })
            .catch(err => console.log(err));

    }

    return (
        <li> <Link to={`/model/${id}/${name}`} >{name}</Link> <button onClick={handleOnClick}><FontAwesomeIcon icon={faTrash} /></button></li>
    );
}

export default Model;
